const ISOStringToDatetime = (ISOString) => {
  const date = new Date(ISOString)
  const weekDay = date.toUTCString().split(" ")[0]
  return date.getUTCHours() + ':' + date.getMinutes() + ' ' + weekDay + ' ' +date.getDate() + '.' + (date.getMonth()+1) + ' ' + date.getFullYear()
}

export default function List(props) {

  return (
    <div class="center">
      {props.data.map((data) => (
        <div>
          <button class="menu-trigger tasklist">
            <div>{data.name}</div>
            <div>{ISOStringToDatetime(data.deadline)}</div>
            <div>{data.description}</div>
            <div>{data.priority}</div>
          </button>
        </div>
      ))}
      <div>{console.log(props.data)}</div>
    </div>
  );
}
