import { useState } from "react";
import DatePicker, { setDefaultLocale } from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import fi from "date-fns/locale/fi";
import PrioritySelect from "./PrioritySelect.js";
const axios = require('axios');

export default function TaskForm(props) {
  const [name, setName] = useState("");
  const [desc, setDesc] = useState("");
  const [datetime, setDatetime] = useState(new Date());
  const [priority, setPriority] = useState("1");
  //const [errors, setErrors] = useState({})

  const handleFormSubmit = async (event) => {
    event.preventDefault();
    await axios.post('https://tamk-4a00ez62-3001-group07.herokuapp.com/tasks', {
      user_id: 1,
      name: name,
      description: desc,
      priority: Number(priority),
      deadline: datetime.toISOString()
    }).then(function (response) {
      console.log(response);
    }).catch(function (error) {
      console.log(error);
    });
    const response = await axios.get("https://tamk-4a00ez62-3001-group07.herokuapp.com/tasks?sort=+deadline");
    const data = response.data
    props.setData(data)
    props.setClicked(false)
  };

  return (
    <form className="taskform" onSubmit={handleFormSubmit}>
      <table>
        <tr>
          <td>
            <label>Task name:</label>
          </td>
          <td>
            <input
              type="text"
              name="name"
              onChange={(event) => setName(event.target.value)}
            ></input>
          </td>
        </tr>
        <tr>
          <td>
            <label>Deadline:</label>
          </td>
          <td>
            <DatePicker
              selected={datetime}
              onChange={(date) => setDatetime(date)}
              locale={fi}
              timeFormat="p"
              dateFormat="Pp"
              showTimeInput
            />
          </td>
        </tr>
        <tr>
          <td>
            <label>Desc:</label>
          </td>
          <td>
            <textarea
              type="text"
              name="desc"
              onChange={(event) => setDesc(event.target.value)}
            ></textarea>
          </td>
        </tr>
        <tr>
          <td>
            <label>Priority:</label>
          </td>
          <td>
            <PrioritySelect
              onChange={(event) => setPriority(event.target.value)}
            ></PrioritySelect>
          </td>
        </tr>
        <input className="menu-trigger" type="submit" value="Add task"></input>
      </table>
    </form>
  );
}
